import React from 'react'
import styled, {createGlobalStyle} from 'styled-components'
import { Link } from 'gatsby'
import backgroundImage from '../images/roman.jpg'

const GlobalStyle = createGlobalStyle`
  body, h1 {
    padding: 0;
    margin: 0;
  }

  *, *::before, *::after {
    box-sizing: border-box;
  }
`;

const StyledWrapper = styled.div`
  width: 100%;
  height: 100vh;
  background: url(${backgroundImage}) no-repeat center/cover;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: top;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
`;

const HeaderItem = styled.h1`
  color: rgba(247, 191, 94, 0.9);
  text-shadow: 1px 1px 5px rgb(68, 68, 68);
  font-size: 5rem;
  font-weight: 700;
`;

const CiteItem = styled.q`
width: 300px;
  margin: 70px 0;
  color: rgba(54, 120, 161, 0.7);
  text-shadow: 1px 1px 1px rgb(68, 68, 68);
  font-size: 2rem;
  font-weight: 700;
  text-align: left;
`;

const LinkItem = styled.a`
a {
  text-decoration: none;
  color: rgb(68, 68, 68);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  font-size: 1.5rem;
  font-weight: 700;
}

a:hover {
  color: rgba(54, 120, 161, 0.86);
}
`;

const IndexPage = () => (
  <>
    <GlobalStyle/>
    <StyledWrapper>
      <HeaderItem>roman</HeaderItem>
      <CiteItem>Coś śmiesznego, plis</CiteItem>
      <LinkItem><Link to={'/'}>Powrót</Link></LinkItem>
    </StyledWrapper>
  </>
)

export default IndexPage